import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { createCar } from '../../../../store/Cars/thunks';
import { Dispatcher } from '../../../../types/store';
import { CarResponse, CarsState } from '../../../../types/Cars';
import { CarForm } from '../../../../types/Edit';

export const useSaveCar = (form: CarForm | null, isEdit: boolean): [CarResponse, boolean, number] => {
  const dispatch = useDispatch<Dispatcher>();
  const { postCar, postLoading, postError } = useSelector<{
    cars: CarsState;
  }, CarsState>((state) => state.cars);
  const [body, setBody] = useState<object | null>(null);

  useEffect(() => {
    if (form) {
      const formData = new FormData();
      formData.append('name', form.name);
      formData.append('description', form.description);
      formData.append('priceMin', '1000');
      formData.append('priceMax', '7500');
      formData.append('thumbnail', form.thumbnail[0]);
      formData.append('colors', JSON.stringify(form.colors));
      formData.append('categoryId', form.categoryId);
      setBody(formData);
    }
  }, [form]);

  useEffect(() => {
    console.log(postCar);
  }, [postCar]);

  useEffect(() => {
    if (body !== null) {
      if (isEdit) {
        console.log('hm');
      } else {
        dispatch(createCar(body));
      }
    }
  }, [body]);

  return [postCar, postLoading, postError];
};
